*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

body {
  height: 100%;
  width: 100vw;
}

img {
  width: 100%;
  height: auto;
}

input, button, textarea, select {
  font: inherit;
}

button {
  border: 0 solid #000;
}

ul {
  list-style: none;
}

a, a:link, a:visited, a:active {
  text-decoration: none;
}

h1 {
  color: #000;
  letter-spacing: .1875rem;
  color: #eeeff4;
  font-family: League Spartan, sans-serif;
  font-size: 9.375rem;
  font-weight: 700;
  line-height: 12.5rem;
}

@media (min-width: 90rem) {
  h1 {
    letter-spacing: -.3125rem;
    font-size: 15.625rem;
    font-weight: 700;
    line-height: 12.5rem;
  }
}

h2 {
  color: #000;
  letter-spacing: -.075rem;
  font-family: League Spartan, sans-serif;
  font-size: 3rem;
  font-weight: 700;
  line-height: 3rem;
}

@media (min-width: 48rem) {
  h2 {
    letter-spacing: -.125rem;
    font-size: 6rem;
    line-height: 5rem;
  }
}

h3 {
  color: #000;
  letter-spacing: -.106875rem;
  font-family: League Spartan, sans-serif;
  font-size: 3rem;
  font-weight: 700;
  line-height: 3.25rem;
}

@media (min-width: 48rem) {
  h3 {
    letter-spacing: -.125rem;
    font-size: 4.5rem;
    line-height: 4rem;
  }
}

h4 {
  color: #000;
  letter-spacing: 0;
  font-family: League Spartan, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
}

body {
  color: #000;
  letter-spacing: 0;
  color: #60636d;
  font-family: League Spartan, sans-serif;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1.5rem;
}

a, a:visited, a:active {
  color: #1b1d23;
}

.header {
  width: 100%;
  z-index: 2;
  opacity: 1;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  transform: translateY(0);
}

.header--hidden {
  width: 100%;
  z-index: 2;
  opacity: 0;
  transition: all .3s ease-in-out;
  position: fixed;
  top: 0;
  transform: translateY(-100%);
}

@media (min-width: 48rem) {
  .header--hidden, .header {
    background-color: #fff;
    justify-content: center;
    margin: 0;
    display: flex;
  }
}

.header__container {
  width: 100vw;
  height: 6rem;
  display: flex;
  position: relative;
}

@media (min-width: 48rem) {
  .header__container {
    width: 100%;
    height: 9.5rem;
    max-width: 57.125rem;
    grid-template-rows: 3.5rem 2.5rem 3.5rem;
    grid-template-columns: 6rem .1fr 1fr;
    margin: 0 6rem;
    display: grid;
  }
}

@media (min-width: 90rem) {
  .header__container {
    max-width: 69.375rem;
    grid-template-columns: 6rem .1fr 1fr;
    margin: 0 10.3125rem;
  }
}

.header__name {
  writing-mode: vertical-rl;
  color: #c8ccd8;
  letter-spacing: 1.125rem;
  font-weight: 500;
  display: none;
  position: absolute;
  top: 0;
  left: -4rem;
}

@media (min-width: 48rem) {
  .header__name {
    align-items: center;
    gap: 3rem;
    display: flex;
  }

  .header__name div {
    height: 6.5rem;
    width: .0625rem;
    background-color: #c8ccd8;
  }
}

@media (min-width: 90rem) {
  .header__name {
    left: -6.875rem;
  }
}

.header__bar {
  width: 100%;
  background-color: #fff;
  grid-area: 1 / 1 / 4 / 4;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  line-height: 0;
  display: flex;
}

@media (min-width: 48rem) {
  .header__bar {
    grid-area: 2 / 1 / 3 / 2;
    margin: 0;
    padding: 0;
  }
}

.header__logo {
  max-width: 4.8125rem;
}

@media (min-width: 48rem) {
  .header__burger {
    display: none;
  }
}

.nav {
  color: #000;
  letter-spacing: 0;
  font-family: League Spartan, sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 2.5rem;
  transition: all .5s ease-in-out;
  position: fixed;
  top: 6rem;
  right: 0;
  transform: translateX(100vw);
}

@media (min-width: 48rem) {
  .nav {
    color: #000;
    letter-spacing: 0;
    z-index: 1;
    grid-area: 2 / 3 / 3 / 4;
    align-self: center;
    font-family: League Spartan, sans-serif;
    font-size: 1.125rem;
    font-weight: 400;
    line-height: 1.5rem;
    position: static;
    top: 0;
    transform: translateX(0);
  }
}

.nav--active {
  transition: all .5s ease-in-out;
  transform: translateX(0);
}

.nav__menu {
  width: 90vw;
  background-color: #eeeff4;
  flex-direction: column;
  gap: 1.0625rem;
  padding: 2.4375rem 0 2.625rem 3rem;
  display: flex;
}

@media (min-width: 48rem) {
  .nav__menu {
    width: 100%;
    z-index: 10;
    transition: all none none;
    background-color: #fff;
    flex-direction: row;
    align-items: center;
    gap: 3.4375rem;
    padding: 0;
    display: flex;
  }

  .nav__item a {
    color: #7d828f;
  }
}

.nav__back {
  width: 100vw;
  height: 100vh;
  z-index: -1;
  opacity: .5;
  background-color: #000;
  transition: display 1s ease-in;
  display: none;
  position: fixed;
  top: 6rem;
}

@media (min-width: 48rem) {
  .nav__back {
    opacity: 1;
    background-color: #fff;
    grid-area: 1 / 1 / 4 / 6;
  }
}

.nav__back--active {
  transition: display 1s ease-in-out;
  display: block;
}

@media (min-width: 48rem) {
  .nav__back--active {
    display: none;
  }
}

