.banner {
  position: relative;
}

.banner__wrapper {
  overflow: hidden;
}

@media (min-width: 48rem) {
  .banner__wrapper {
    margin: 0 6rem;
  }
}

@media (min-width: 90rem) {
  .banner__wrapper {
    margin: 0;
  }
}

.banner__container {
  transition: all 1s ease-in-out;
  display: flex;
  transform: translateX(0%);
}

.banner__slide {
  width: 100%;
  flex-shrink: 0;
  grid-template-rows: .1fr 1fr .1fr;
  grid-template-columns: .1fr 1fr .1fr;
  align-items: center;
  display: grid;
}

@media (min-width: 48rem) {
  .banner__slide {
    grid-template-rows: .15fr 1fr .15fr;
    grid-template-columns: .15fr 1fr .15fr;
  }
}

@media (min-width: 90rem) {
  .banner__slide {
    grid-template-rows: .25fr 1fr .25fr;
    grid-template-columns: .25fr 1fr .25fr;
  }
}

.banner__top {
  color: #fff;
  grid-area: 2 / 2 / 3 / 3;
}

.banner__heading {
  color: #fff;
  max-width: 34rem;
}

.banner__para {
  max-width: 27.8125rem;
  margin-top: .625rem;
  margin-bottom: 2.5625rem;
}

.banner__bottom {
  z-index: -1;
  grid-area: 1 / 1 / 4 / 4;
  line-height: 0;
  position: relative;
}

.banner__overlay {
  z-index: 1;
  width: 100%;
  height: 100%;
  opacity: .25;
  background-color: #000;
  position: absolute;
  top: 0;
  right: 0;
}

.banner__img {
  height: 35rem;
